<template>
    <section class="contact-me container">
        <h1 class="contact-me__title">Have a Project?</h1>
        <div class="contact-me__subtitle">
            <p>If you're looking for a professional and stunning website for your business, startup, or personal brand, I'm here to help.</p>
        </div>
        <form v-if="!succesMessage & !errorMsg" class="contact-form" method="POST" @submit.prevent="sendEmail">
            <div class="input-group">
                <label class="input-group__label">Your Name <span class="required">*</span></label>
                <input v-model="nameMsg" class="input-group__input" type="text" required>
            </div>
            <div class="input-group">
                <label class="input-group__label">Your Email <span class="required">*</span></label>
                <input v-model="emailMsg" class="input-group__input" type="email" required>
            </div>
            <div class="input-group">
                <label class="input-group__label">Your Name <span class="required">*</span></label>
                <textarea v-model="messageMsg" class="input-group__textarea" required></textarea>
            </div>
            <button :disabled="!nameMsg || !emailMsg || !messageMsg" type="submit" class="btn btn_lg mt-4">{{ buttonMsg }}</button>
        </form>
        <div v-if="succesMessage & !errorMsg" class="success-message">
            <p class="success-message__title">Success!</p>
            <p class="success-message__subtitle">Your message was sent. I'll make sure to answer you as soon as possible!</p>
        </div>
        <div v-if="!succesMessage & errorMsg != ''" class="error-message">
            <p class="error-message__title">Error!</p>
            <p class="error-message__subtitle">{{ errorMsg }}</p>
        </div>
        <div class="socials">
            <p class="socials__items socials__items_title">BASED IN KAZAN, AVAILABLE WORLDWIDE</p>
            <div class="socials__items socials__items_email">
                <a href="mailto:arteildev@gmail.com" class="link">arteildev@gmail.com</a>
            </div>

            <p class="socials__items socials__items_title">Social</p>
            <div class="socials_column">
                <div class="socials__items">
                    <a href="https://t.me/arteilzx" class="link">Telegram</a>
                </div>
                <div class="socials__items">
                    <a href="#" class="link">Instagram</a>
                </div>
                <div class="socials__items">
                    <a href="#" class="link">VK</a>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import axios from 'axios'

export default {
    data() {
        return {
            nameMsg: '',      
            emailMsg: '',      
            messageMsg: '',
            buttonMsg: 'Send Message',
            succesMessage: false,
            errorMsg: ''
        }
    },

    methods: {
        sendEmail() {
            this.buttonMsg = 'Please wait...'
            axios.post('https://formspree.io/f/mbjezrkj',{
                name: this.nameMsg,
                from: this.emailMsg,
                _subject: `${this.nameMsg} | Message from arteildev.github.io`,
                message: this.messageMsg,
            }).then((response) => {
                this.nameMsg = '';
                this.emailMsg = '';
                this.messageMsg = '';
                this.buttonMsg = 'Send Message'
                this.succesMessage = true
            }).catch((error) => {
                console.log(error.message)
                if(error) {
                    this.errorMsg = error.message;
                }
            })
        }
    }
}
</script>